import generateSignString from './setsign'

const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: process.env.NODE_ENV === 'development' ? 'http://rw18e3z2i.hb-bkt.clouddn.com/' :
			'http://rw18e3z2i.hb-bkt.clouddn.com/',
		// baseUrl: process.env.NODE_ENV === 'development' ? 'https://thb.tmsx.net' :
		// 	'https:/thb.tmsx.net',
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		originalData: true,
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
	});
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		let {Token} = res.header
		if(Token){
			vm.$store.commit('SET_TOKEN',Token)
		}
		let code = Number(res.statusCode || res.errMsg)
		if (code == 200) {
			// originalDatatrue
			// res.statueCoderes.data.code
			// if (res.data.code == '200') {
				// originalDatatruereturnthis.$u.postthen
				return res.data;
			// } 
			// else {
			// 	vm.$u.toast(res.data.msg)
			// 	return false
			// };
		} else {
			switch (code) {
				case 401:
					vm.$u.route({
						// type: 'redirect',
						url: '/pages/login/login',
					});
					break
				case 422:
					var thisErr = JSON.parse(res.data.msg);
					vm.$u.toast(thisErr[0].error[0]);
					break
				case 429:
					vm.$u.toast('')
					break
				case 500:
					vm.$u.toast('')
					break
				case 501:
					vm.$u.toast('')
					break
				case 502:
					vm.$u.toast('')
					break
				case 503:
					vm.$u.toast('')
					break
				case 504:
					vm.$u.toast('')
					break
				case 505:
					vm.$u.toast('http')
					break
				default:
					vm.$u.toast(`${code}`)
			}
			return false
		}

	}
}

export default {
	install
}
